import * as React from "react";
import * as ReactDOM from "react-dom";
import {
  DropTarget,
  DropTargetConnector,
  DropTargetMonitor,
  ConnectDropTarget,
  DropTargetSpec,
  ClientOffset,
} from "react-dnd";
import {connect} from "react-redux";
import * as Styles from "components/Canvas.css";
import {IAppState, IAction, Shape, ShapeType} from "actions";
import {shapeFactory} from "components/shapes/Shapes";

const boxTarget: DropTargetSpec<ICanvasProps & ICanvasDropProps & IDispatch> = {
  drop(props: IDispatch, monitor?: DropTargetMonitor, component?: React.Component<ICanvasDropProps>): object {
    const node = ReactDOM.findDOMNode(component as React.ReactInstance);
    const rect = (node as Element).getBoundingClientRect() as DOMRect;

    const clientOffset = (monitor && monitor.getSourceClientOffset()) as ClientOffset;

    return {name: "Canvas", x: clientOffset.x - rect.x, y: clientOffset.y - rect.y};
  },
};

function mapStateToProps(state: IAppState): ICanvasProps {
  return state.canvas;
}

const collect = (connector: DropTargetConnector, monitor: DropTargetMonitor) => ({
  connectDropTarget: connector.dropTarget(),
  isOver: monitor.isOver(),
  canDrop: monitor.canDrop(),
});

interface ICanvasProps {
  shapes: Shape[];
}

interface ICanvasDropProps {
  canDrop?: boolean;
  isOver?: boolean;
  connectDropTarget?: ConnectDropTarget;
}

interface IDispatch {
  dispatch(action: IAction): void;
}

class CanvasBase extends React.Component<ICanvasProps & ICanvasDropProps & IDispatch> {
  public render(): JSX.Element {
    const {canDrop, isOver, connectDropTarget, shapes} = this.props;

    return (connectDropTarget as ConnectDropTarget)(
      <svg className={Styles.canvas}>{shapes.map(this.renderShape)}</svg>
    );
  }

  private renderShape(s: Shape): JSX.Element {
    const {shapeType, x, y} = s;
    return shapeFactory(shapeType, {x, y});
  }
}

export const Canvas = connect(mapStateToProps)(DropTarget("SHAPE", boxTarget, collect)(CanvasBase));
