import * as React from "react";
import * as ReactDOM from "react-dom";
import {Provider} from "react-redux";
import {createStore} from "redux";

import {EditingPane} from "components/EditingPane";
import {IAppState, TAction, IShapeDropAction} from "actions";

const initialState = {
  palette: {selectedShapeType: undefined},
  canvas: {shapes: []},
};

function reducer(state: IAppState = initialState, action: TAction): IAppState {
  switch (action.type) {
    case "SELECT_SHAPE":
      return {
        palette: {
          selectedShapeType: action.shapeType,
        },
        canvas: state.canvas,
      };
    case "DROP_SHAPE":
      const dropAction: IShapeDropAction = action as IShapeDropAction;
      return {
        palette: state.palette,
        canvas: {
          shapes: state.canvas.shapes.concat([
            {
              shapeType: dropAction.shapeType,
              x: dropAction.x,
              y: dropAction.y,
            },
          ]),
        },
      };
  }
  return state;
}

const store = createStore(reducer);

ReactDOM.render(
  <Provider store={store}>
    <EditingPane />
  </Provider>,
  document.getElementById("editor")
);
