export interface IAppState {
  palette: {
    selectedShapeType?: ShapeType;
  };
  canvas: {
    shapes: Shape[];
  };
}

export interface IAction {
  type: string;
}

export interface IPaletteAction extends IAction {
  shapeType: ShapeType;
}

export interface IShapeDropAction extends IPaletteAction {
  x: number;
  y: number;
}

export type TAction = IPaletteAction | IShapeDropAction;

export const selectShapeOnPalette = (shapeType: ShapeType) => {
  return {type: "SELECT_SHAPE", shapeType};
};

export const dropShapeToCanvas = (shapeType: ShapeType, x: number, y: number) => {
  return {type: "DROP_SHAPE", shapeType, x, y};
};

export enum ShapeType {
  Circle = 1,
  Square = 2,
  Triangle = 3,
}

export interface Shape {
  shapeType: ShapeType;
  x: number;
  y: number;
}
